import React from 'react'
import PersonCard from '../../components/person card/PersonCard'
import { PersonalData } from '../../components/person card/Data'
import styles from './About.module.scss'
const About = () => {

    return (
        <div className={styles.about}>
            <div className={styles.baner}></div>
            <div className={styles.container}>
                <header>
                    <h1 className={styles.title}>Sw o nas kilka</h1>
                </header>
                <p className={styles.desc}>Kajakpol to ceniona i rozpoznawalna marka wrd caego rodowiska mionikw kajakw oraz szeroko pojtej rozrywki wodnej. Firma zostaa zaoona przez dwch pasjonatw kajakarstwa w 1997 r. urodzonych w Giycku. Pocztkowo niewielki interes zacz si dynamicznie rozwija i obejmow swoim zasigiem coraz to wikszy obszar Polski. Aktualnie posiadamy, obok gwnej bazy w Giycku, dwie dodatkowe placwki ulokowane w Krutyni oraz w Mikoajkach. Posiadamy wieloletnie dowiadczenie w organizowaniu spyww, wyporzyczaniu sprztu wodnego, prowadzeniu kursw, wszystko to pod opiek i nadzorem naszej wyszkolonej kadry.</p>

                {
                    PersonalData.map(person => <PersonCard key={person.id} person={person}/>)
                }

            </div>




        </div>
    )
}

export default About
